    -webkit-appearance: none;
    outline: none;
    padding: 3px 35px 3px 15px;
    height: 100%;
    font-size: 15px;
    font-family: "roboto_condensedlight"; }
    .select-standard select:focus {
      border-color: #11b6f0;
